/**
 * 
 * @author zs.duan
 * @date 2024-08-02
 * @param {object} option 
 * @param {Array} option.data 
 * @param {string} [option.key] key
 * @param {string} [option.children] key
 * @param {string} [option.value] 
 * @param {Function} [option.isCompletely] 
 * @param {Function} [option.success] 
 * @param {Function} [option.fail] 
 * @returns {Array} 
 * @example search({
 *      data : [],
 *      value : "",
 *      success : (reslut) =>{},
 *      fail : (error) =>{}
 * })
 * */ 

export default function Search(option){
    let options = {
        data : [],
        key : "",
        children : "",
        value : "",
        isCompletely : false,
        success : (reslut) =>{},
        fail : (error) =>{}
    }
    Object.assign(options, option);
    if (!options.data){
        options.fail({code : -1, msg : "" });
        throw new Error("");
    }
    if(!Array.isArray(options.data)){
        options.fail({code : -1, msg : "" });
        throw new Error("");
    }
    if(typeof options.value !== 'string'){
        options.fail({code : -1, msg : "" });
        throw new Error("");
    }
    if(options.key && typeof options.key !== 'string'){
        options.fail({code : -1, msg : "key" });
        throw new Error("key");
    }
    if(typeof options.data[0] == 'object' && !options.key){
        options.fail({code : -1, msg : ",key" });
        throw new Error(",key");
    }
    if(!options.value){
        options.success(options.data);
        return options.data;
    }

    let reslut = [];
    const reg = new RegExp((options.value).toString());
    // key
    const _searchByKey = (data) => {
        data.forEach(element => {
            if(options.isCompletely && element[options.key] === options.value){
                reslut.push(element)
            }else{
                element[options.key] = element[options.key].toString();
                if(element[options.key].match(reg)){
                    reslut.push(element)
                }
            }
            if(options.children && element[options.children]){
                _searchByKey(element[options.children]);
            }
        });
    }

    // 
    const _search = (data) => {
        data.forEach(element => {
            if(options.isCompletely && element === options.value){
                reslut.push(element)
            }else{
                if(element.toString().match(reg)){
                    reslut.push(element)
                }
            }
            if(options.children && element[options.children]){
                _search(element[options.children]);
            }
        })
    }

    const _formatTree = (data) => {
        data.forEach(element => {
            
        })
    }

    if(options.key){
        _searchByKey(options.data);
    }else{
        _search(options.data);
    }
    reslut.forEach(element => {
        delete element[options.children];
    });
    options.success(reslut);
    return reslut;
}