<template>
    <div class="dzs-notice-bar" :style="{backgroundColor : backgroundColor}" v-if="showNoticeBar">
        <div class="left-icon" v-if="showIcon">
            <slot name="left-icon"></slot>
            <i class="icon" :style="{color:color}" :class="icon" v-if="icon && !imgSrc"></i>
            <img class="img" :src="imgSrc" v-if="imgSrc" />
        </div>
        <div class="content-box" @click="contentClick">
            <slot></slot>
            <span v-if="!isEline" :style="{color:color , animationDuration : speeds}" :class="['text' , isMove && 'text-move' ] " >{{text}}</span>
            <div v-if="isEline" :style="{color:color}" class="eline-text">{{text}}</div>
        </div>
        <div class="right-icon" @click="close" v-if="showCloseIcon" >
            <slot name="right-icon"></slot>
            <i class="icon" :style="{color:color}" :class="closeIcon" v-if="closeIcon && !closeImgSrc"></i>
            <img class="img" :src="closeImgSrc" v-if="closeImgSrc" />
        </div>
    </div>
</template>
<script>
/**
 * @name : dzs-notice-bar 
 * @description : 
 * @date : 2021-06-28 13:56:00
 * @version : V1.0.0
 * @Author : zs.duan
 * 
 * @param {String} icon  icon
 * @param {String} imgSrc  icon icon
 * @param {String} closeIcon  icon
 * @param {String} closeImgSrc  icon closeIcon
 * @param {String} text 
 * @param {Number | String} speed 
 * @param {Boolean} isMove 
 * @param {Boolean} isEline 
 * @param {Boolean} showIcon icon
 * @param {Boolean} showCloseIcon icon
 * @param {String} color 
 * @param {String} backgroundColor 
 * 
 * @method close 
 * @method contentClick 
 * @method closeNoticeBar   ref
 * 
 * @slot left-icon icon
 * @slot right-icon icon
 * @slot 
 * */ 
export default{
    name : "dzsNoticeBar",
    props : {
        /** icon*/
        icon : {
            type : String,
            default : ()=>{
                return "el-icon-bell"
            }
        },
        /** icon icon*/
        imgSrc : {
            type : String,
            default : ()=>{
                return ""
            }
        },
        /** icon*/
        closeIcon : {
            type : String,
            default : ()=>{
                return "el-icon-close"
            }
        },
        /** icon closeIcon*/
        closeImgSrc : {
            type : String,
            default : ()=>{
                return ""
            }
        },
        /***/
        text : {
            type : String,
            default : ()=>{
                return ""
            }
        },
        /***/
        speed : {
            type : Number | String,
            default : ()=>{
                return 5
            }
        },
        /***/
        isMove : {
            type : Boolean,
            default : ()=>{
                return false
            }
        },
        /***/
        isEline : {
            type : Boolean,
            default : ()=>{
                return false
            }
        },
        /**icon*/
        showIcon : {
            type : Boolean,
            default : ()=>{
                return true
            }
        },
        /**icon*/
        showCloseIcon : {
            type : Boolean,
            default : ()=>{
                return true
            }
        },
        /***/ 
        color : {
            type : String,
            default : ()=>{
                return "#66b1ff"
            }
        },
        /***/
        backgroundColor : {
            type : String,
            default : ()=>{
                return "rgba(102,177,255,0.2)"
            }
        }
    },
    computed : {
        speeds(){
            if(typeof this.speed == "number"){
                return this.speed + "s"
            }else if(typeof this.speed == "string"){
                return this.speed
            }
        },
        /**content-box */
        contentBoxWidth(){
            if(this.showIcon && this.showCloseIcon){
                return "calc(100% - 60px)"
            }else if(this.showIcon || this.showCloseIcon){
                return "calc(100% - 30px)"
            }else{
                return "100%"
            }
        }
    },
    data(){
        return {
            showNoticeBar : true
        }
    },
    methods : {
        close(){
            this.closeNoticeBar();
            this.$emit("close")
        },
        contentClick(){
            this.$emit("noticeClick")
        },
        /**natice*/ 
        closeNoticeBar(){
            this.showNoticeBar = false;
        }
    },
}
</script>
<style lang="scss" scoped>
.dzs-notice-bar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 10px;
    width: 100%;
    background-color: v-bind(backgroundColor);
    border-radius: 2px;
    cursor: pointer;
    .left-icon,.right-icon{
        width: 30px;
        height: 20px;
        padding-top: 1px;
        .icon{
            font-size: 20px;
            color: v-bind(color);
        }
        .img{
            width: 20px;
            height: 20px;
            object-fit: cover;
        }
    }
    .right-icon{
        cursor: pointer;
        margin-left: 20px;
    }
    .content-box{
        flex: 1;
        width: v-bind(contentBoxWidth);
        overflow: hidden;
        .text{
            color: v-bind(color);
            display: block;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
        }
        .eline-text{
            color: v-bind(color);
        }
        // 
        .eline{
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
        }
        // 
        .text-move{
            display: inline-block;
            animation: move v-bind(speeds) linear infinite;
        }
        @keyframes move{
            0%{
                transform: translateX(0);
            }
            100%{
                transform: translateX(-100%);
            }
        }


    }
}
</style>
